.frame .agenda {
    color: #083d14;
    font-family: "Helvetica-Bold", Helvetica;
    font-size: 1em;
    font-weight: 700;
    letter-spacing: -0.4px;
    line-height: 19.2px;
    margin: -1vh;
    margin-top: 16vh;
    border-bottom-color: #000000;
    border-width: 1px;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;
    border-color: black;
    background: none;
    cursor: pointer;
    margin-bottom: -20px;
    }
    .frame .agenda:hover {
        color: white;
        background-color: #083d14;
    }

.frame .conoce {
color: #000000;
font-family: "Helvetica-Light", Helvetica;
font-size: 1em;
font-weight: 300;
letter-spacing: -0.4px;
line-height: 19.2px;
margin: 0;
margin-top:8px;
text-decoration: none;
margin-bottom: 12px;

}
a {
    text-decoration: none;
    color: black;
}
.frame .conoce  a:link a:visited {
    color: black;
    border-bottom-color: #000000;
    border-width: 1px;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;
    border-color: black;
}
.frame .conoce a:hover {
    color: white;
    background-color: #083d14;
    
}
